.simplebar-track.simplebar-horizontal {
  height: 4px;
  background-color: #ededed;
  border-radius: 2px;
  margin-right: 20px;
  margin-left: 20px;
}

@media (min-width: 769px) {
  .simplebar-track.simplebar-horizontal {
    margin-right: 128px;
    margin-left: 128px;
  }
}

.simplebar-track.simplebar-horizontal .simplebar-scrollbar {
  top: 0;
  height: 4px;
}

.simplebar-track.simplebar-horizontal .simplebar-scrollbar::before {
  left: 0;
  right: 0;
  background-color: #0062ff;
  border-radius: 2px;
  opacity: 1;
}

.simplebar-mask {
  right: 20px;
}

@media (min-width: 769px) {
  .simplebar-mask {
    right: 0;
  }
}

@media (min-width: 769px) {
  .simplebar-content {
    display: flex;
    flex-flow: column wrap;
    align-items: center;
  }
}

.rating {
  display: flex;
  flex-flow: column wrap;
}

.rating__title {
  margin: 0;
  margin-bottom: 10px;
  padding: 0;
  font-family: Consolas, Arial, sans-serif;
  font-size: 16px;
  line-height: 18px;
  font-weight: 700;
}

@media (min-width: 769px) {
  .rating__title {
    font-size: 18px;
  }
}

.rating__list {
  padding-left: 0;
  list-style: none;
  display: flex;
  flex-flow: row nowrap;
  flex-flow: row-reverse;
  justify-content: flex-end;
  padding: 0;
  margin: 0;
}

.rating__item {
  cursor: pointer;
}

.rating__item:hover .icon,
.rating__item:hover ~ .rating__item .icon {
  fill: black;
  --border: #ffb400;
  --fill: #ffb400;
}

.rating__item:not(:first-of-type) {
  margin-right: 10px;
}

.rating__item > * {
  pointer-events: none;
}

.rating__item--active .icon,
.rating__item--active ~ .rating__item .icon {
  fill: black;
  --border: #fbb100;
  --fill: #fbb100;
}

.slider {
  display: flex;
  width: 100%;
  margin: auto;
  padding-bottom: 60px;
  transition: all 0.5s;
}

@media (min-width: 769px) {
  .slider {
    padding-bottom: initial;
  }
}

.slider__inner {
  padding-left: 0;
  list-style: none;
  margin: 0;
  display: flex;
  justify-content: space-between;
  width: inherit;
  transition: all 0.5s;
}

@media (min-width: 769px) {
  .slider__inner {
    margin-right: 15px;
    margin-left: 15px;
  }
}

.slider__item {
  display: flex;
  width: 100%;
}

@media (min-width: 769px) {
  .slider__item {
    max-width: calc(100% / 3 - 30px);
  }
}

.slider__link {
  display: flex;
  width: inherit;
  text-decoration: none;
}

.slider__figure {
  display: flex;
  flex-flow: column wrap;
  width: inherit;
  margin: 0;
  padding: 0;
}

.slider__figure picture {
  display: flex;
  width: inherit;
}

.slider__image {
  display: flex;
  width: 100%;
  height: auto;
  object-fit: cover;
  margin-bottom: 10px;
}

.slider__caption {
  font-family: Consolas, Arial, sans-serif;
  font-size: 18px;
  line-height: 24px;
  color: #272522;
  hyphens: auto;
}

.swiper {
  display: block;
}

@media (min-width: 769px) {
  .swiper-pagination {
    display: none;
  }
}

.swiper-container-horizontal > .swiper-pagination-bullets {
  display: flex;
  justify-content: center;
  align-items: center;
}

.swiper-container-horizontal > .swiper-pagination-bullets .swiper-pagination-bullet {
  margin: 0 10px;
}

.swiper-container-horizontal > .swiper-pagination-bullets .swiper-pagination-bullet-active {
  width: 14px;
  height: 14px;
  background-color: #0062ff;
}

.verdict {
  display: flex;
  flex-flow: column wrap;
  min-width: 320px;
  max-width: 1024px;
  margin: auto;
}

.verdict__container {
  padding-right: 20px;
  padding-left: 20px;
  padding-top: 30px;
  padding-bottom: 35px;
  transition: all 0.5s;
}

@media (min-width: 769px) {
  .verdict__container {
    padding-right: 128px;
    padding-left: 128px;
    padding-top: 50px;
    padding-bottom: 70px;
  }
}

.verdict__title {
  margin: 0;
  margin-bottom: 15px;
  padding: 0;
  font-family: Consolas, Arial, sans-serif;
  font-size: 20px;
  line-height: 36px;
  font-weight: 400;
  color: #000;
  transition: all 0.5s;
}

@media (min-width: 769px) {
  .verdict__title {
    position: relative;
    width: 100%;
    padding-right: 15px;
    padding-left: 15px;
    flex: 0 0 100%;
    max-width: 100%;
    font-size: 28px;
    margin-bottom: 25px;
  }
}

.verdict__text {
  margin: 0;
  padding: 0;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 14px;
  line-height: 18px;
  color: #202326;
  transition: all 0.5s;
}

@media (min-width: 769px) {
  .verdict__text {
    position: relative;
    width: 100%;
    padding-right: 15px;
    padding-left: 15px;
    flex: 0 0 100%;
    max-width: 100%;
    font-size: 16px;
    line-height: 24px;
  }
}
